
.cart {
    position: relative;
    width: 11.5em; /* Width of the card */
    overflow: hidden;
    cursor: pointer;
    transition: transform .2s ease-in-out;
    margin-bottom: 1.5em;
  }
  .cart-img:hover {
    transform: translateY(-5px);
  }
  .flexA { 
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .cart-img {
    width: 100%;
    transition: transform .2s ease-in-out;
    border-radius: 14%;
    height: 11.5em; /* Height of the image */
    object-fit: cover;
  }
  
  .cart-top-icons {
    position: absolute;
    top: 0.5em;
    left: 0.5em;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.2em;
  }
  
  .rating {
    gap: 0.5em;
    font-size: 0.9em;
    font-weight: 600;
    color: #f8f8f8;
    display: flex;
    background-color: #00000058;
    align-items: center;
    padding: 0.4em 0.8em; /* Adds padding inside the rating box */
    border-radius: 0.8em; /* Rounds the corners of the rating box */
    backdrop-filter: blur(20px); /* Blurs the background behind the rating */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.241); /* Adds a slight shadow for more depth */
  }
  
  
  .cart-title {
    font-size: 1em;
    font-weight: 650;
    color: #000000;
    margin-top: 0.6em;
  }
  .cart-title:hover {
    color: #1d1d1d;;
  }
  
  .cart-price {
    font-size: 1.3em;
    color: #151515;
    font-weight: 800;
    margin-top: 0.6em;
    transition: color .2s ease-in-out;
  }
  .cart-price:hover {
    color: #2a2a2a;
  }
  
  .cart-button {
    background-color: #1a1a1a;
    color: #fff;
    border: none;
    border-radius: 50%;
    padding: 0.8em;
    display: flex;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color .2s ease-in-out;
  }
  
  .cart-button:hover {
    background-color: #3a3a3a;
  }
  