.products {
    display: flex;
    flex-wrap: wrap; /* Позволяет элементам переноситься на новую строку при необходимости */
    justify-content: center; /* Выравнивает карточки по центру горизонтально */
    gap: 1em; /* Расстояние между карточками */
    padding: 1em; /* Отступы внутри контейнера */
  }
  
  .product-card {
    flex: 1 1 calc(25% - 32px); /* Ширина карточек, чтобы они занимали 25% ширины контейнера с учетом отступов */
    max-width: calc(25% - 32px); /* Максимальная ширина карточек */
    box-sizing: border-box; /* Учитывает отступы и границы в ширину элемента */
  }
  