.App { 
  padding: 0.4em;
}

.title { 
  display: flex;
  align-items: center;
  justify-content: center; /* Центрируем содержимое по горизонтали */
  gap: 0.6em;
  border: 1px solid rgb(162, 162, 162);
  padding: 0.8em;
  border-radius: 1em;
  margin: 0 auto; /* Центрируем контейнер в родительском элементе */
  max-width: 600px; /* Устанавливаем максимальную ширину, если нужно */
}

.title-text {
  font-size: 1.4em;
  font-weight: 800;
  color: #007bff;
}

.title-text b {
  color: #202429;
}

.title-img { 
  width: 2em;
}
