.categor-title { 
    font-size: 1.3em;
    font-weight: 700;
    margin-bottom: 0.8em;
    margin-top: 1em;
}

.categor-items { 
    display: flex;
    align-items: center;
    gap: 1em;
}

.categor-item { 
    font-size: 1em;
    font-weight: 600;
    border: 1px rgb(165, 165, 165) solid;
    padding: 0.6em 1em;
    border-radius: 3em;
    color: rgb(52, 52, 52);
    cursor: pointer;
    transition: background-color 0.3s ease; /* Плавный переход для фона */
}

.categor-item:hover {
    background-color: #f0f0f0; /* Легкое изменение фона при наведении */
}

.categor-item.active {
    background-color: #007bff; /* Фон для активного элемента */
    color: #fff; /* Цвет текста для активного элемента */
    border-color: #007bff; /* Цвет границы для активного элемента */
}

.container {
    display: flex;
    flex-wrap: wrap; /* Позволяет элементам переноситься на новую строку при необходимости */
    justify-content: center; /* Выравнивает карточки по центру горизонтально */
    gap: 1em; /* Расстояние между карточками */
    margin-bottom: 0.8em;
}
